@import url(../../node_modules/normalize.css/normalize.css);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  font-size: 62.5%; }
  @media only screen and (max-width: 800px) {
    html {
      font-size: 55%; } }
  @media only screen and (max-width: 600px) {
    html {
      font-size: 50%; } }

html,
body {
  height: 100%; }

body {
  background: #f2f2f2; }

img {
  height: 100%;
  width: auto; }

.heading_primary {
  font-size: 4rem;
  font-weight: 700;
  color: #531e09; }

.heading_second {
  font-size: 1.6rem; }

.heading_small {
  font-size: 1.5rem;
  color: #aaaaaa; }

.quote {
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 2.5rem;
  color: #aaaaaa; }
  .quote_author {
    font-size: 1.2rem;
    font-weight: 600;
    color: #aaaaaa; }

.paragraph_small {
  font-size: 1.1rem;
  font-weight: 600;
  color: #aaaaaa; }

@keyframes toDown {
  0% {
    opacity: 0;
    transform: translateY(-5rem); }
  80% {
    transform: translateY(1rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

@keyframes toRight {
  0% {
    opacity: 0;
    transform: translateX(-5rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

@keyframes toUp {
  0% {
    opacity: 0;
    transform: translateY(5rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

@keyframes reflection {
  0% {
    opacity: 0;
    transform: translateX(2px) rotate(-40deg) translateY(-45px); }
  100% {
    opacity: 1;
    transform: translateX(2px) rotate(-40deg) translateY(45px); } }

.container {
  display: flex;
  width: 100vw;
  background: #f2f2f2;
  height: 100%;
  position: relative;
  overflow: hidden; }

.cards {
  grid-area: cards;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(auto-fill, 150px);
  grid-gap: 10px;
  padding-bottom: 20px; }
  @media only screen and (max-width: 1260px) {
    .cards {
      grid-template-columns: repeat(auto-fill, 100px); } }
  @media only screen and (max-width: 800px) {
    .cards {
      grid-gap: 0; } }
  @media only screen and (max-width: 400px) {
    .cards {
      display: none; } }
  .cards_title {
    grid-area: title;
    animation-name: toRight;
    animation-duration: .8s;
    animation-timing-function: ease-out; }
    @media only screen and (max-width: 400px) {
      .cards_title {
        display: none; } }

.intro {
  display: grid;
  grid-template-rows: 80px 1fr min-content 1fr;
  grid-template-columns: 1fr 4fr 5%;
  grid-template-areas: ". logo ." ". claim ." ". title ." ". cards .";
  width: 70vw;
  height: 100vh;
  background: #f2f2f2;
  color: #531e09; }
  @media only screen and (max-width: 600px) {
    .intro {
      grid-template-columns: 10% 4fr 10%;
      width: 100vw; } }
  @media only screen and (max-width: 400px) {
    .intro {
      grid-template-rows: 80px 1fr min-content;
      grid-template-columns: 10% 4fr 10%;
      grid-template-areas: ". logo ." ". claim ." ". title ."; } }
  .intro_logo {
    grid-area: logo;
    align-self: end;
    height: 3.5rem; }
  .intro_claim {
    max-width: 70rem;
    grid-area: claim;
    align-self: center;
    animation-name: toDown;
    animation-duration: .8s;
    animation-timing-function: ease-out; }

.card {
  animation-name: toUp;
  animation-timing-function: ease-out; }
  .card--1 {
    animation-duration: .3s; }
  .card--2 {
    animation-duration: .6s; }
  .card--3 {
    animation-duration: .9s; }
  .card--4 {
    animation-duration: 1.2s; }
  .card_img {
    height: 15rem;
    width: 15rem;
    border-radius: 5px;
    margin: 20px 0;
    transition: .3s; }
    @media only screen and (max-width: 1260px) {
      .card_img {
        height: 10rem;
        width: 10rem; } }
    .card_img--1 {
      background-image: url("../assets/img/img01c.jpg");
      background-size: cover; }
    .card_img--2 {
      background-image: url("../assets/img/img02c.jpg");
      background-size: cover; }
    .card_img--3 {
      background-image: url("../assets/img/img03c.jpg");
      background-size: cover; }
    .card_img--4 {
      background-image: url("../assets/img/img04c.jpg");
      background-size: cover; }
  .card_rating {
    display: flex;
    margin: 10px 0; }
    .card_rating_item {
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background: #a2c31d; }
      .card_rating_item:not(:last-child) {
        margin-right: 3px; }

.video {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(65%);
  transition: all .5s;
  background: transparent; }
  @media only screen and (max-width: 1360px) {
    .video {
      transform: translateX(70%); } }
  @media only screen and (max-width: 800px) {
    .video {
      transform: translateX(80%); } }
  .video_bg {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 1;
    clip-path: polygon(10% 0%, 100% 0%, 100% 100%, 10% 100%, 0% 50%);
    transition: all 1s; }
    @media only screen and (max-width: 600px) {
      .video_bg {
        clip-path: polygon(-40% 0, 100% 0, 100% 100%, 30% 15%, -30% 14%); } }
    @media only screen and (max-width: 400px) {
      .video_bg {
        clip-path: polygon(-40% 0, 100% 0, 100% 100%, 30% 40%, -27% 25%); } }
    .video_bg_content {
      height: 100%;
      width: 100%;
      object-fit: cover; }
  .video_open, .video_close {
    display: none; }
  .video_btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 7rem;
    width: 7rem;
    position: absolute;
    z-index: 2000;
    border-radius: 50%;
    text-align: center;
    cursor: pointer;
    transform: translateY(-50%);
    transition: .3s; }
    .video_btn--open {
      position: relative;
      top: 50%;
      left: 6%;
      background-color: #a2c31d;
      border: solid 1px #daee8b;
      box-shadow: 0 1rem 3rem rgba(162, 195, 29, 0.1);
      overflow: hidden; }
      @media only screen and (max-width: 600px) {
        .video_btn--open {
          top: 8%;
          left: 0; } }
      .video_btn--open::before {
        content: "";
        position: absolute;
        height: 3px;
        width: 12rem;
        background: #f2f2f2;
        transform-origin: center;
        transform: translateX(2px) rotate(-40deg) translateY(-45px);
        animation-name: reflection;
        animation-timing-function: ease-out;
        animation-duration: 1s;
        animation-delay: 2s;
        opacity: .8;
        filter: blur(3px); }
      .video_btn--open:hover {
        background-color: #90ad1a; }
    .video_btn--close {
      top: 10%;
      right: 10%;
      background-color: #531e09;
      border: solid 1px #812f0e;
      box-shadow: 0 1rem 3rem rgba(83, 30, 9, 0.1);
      opacity: 0;
      visibility: hidden; }
      .video_btn--close:hover {
        background-color: #3c1607; }
  .video_icon--open {
    height: 3rem;
    width: 3rem;
    line-height: 0;
    fill: #f2f2f2; }
  .video_icon--close {
    height: 2rem;
    width: 2rem;
    line-height: 0;
    fill: #f2f2f2; }

.video_open:checked ~ .video {
  transform: translateX(0%);
  background: #f2f2f2; }

.video_open:checked ~ .video .video_btn--open {
  opacity: 0;
  visibility: hidden; }

.video_open:checked ~ .video .video_btn--close {
  opacity: 1;
  visibility: visible; }

.video_open:checked ~ .video .video_bg {
  border-radius: 0px;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%); }

.video_open:checked ~ .video .video_bg_content {
  filter: brightness(20%); }

.video_open:checked ~ .video .video_frame {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%);
  display: block; }

.video_frame {
  width: 100%;
  max-width: 853px;
  margin: 0 auto;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%);
  opacity: 0;
  display: none;
  visibility: hidden;
  transition: .2s; }
  .video_frame_wrapper {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0px 2px 30px -4px rgba(37, 37, 37, 0.75); }
    .video_frame_wrapper iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }

.u-margin-top-none {
  margin-top: 0; }

.u-margin-top-sm {
  margin-top: 10px; }

.u-margin-bottom-sm {
  margin-bottom: 20px; }

.u-text-uppercase {
  text-transform: uppercase; }
